/* styles.css */

body {
    margin: 0;
    font-family: 'Arial', sans-serif;
  }
  
  header {
    background-color: #333;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .logo {
    font-size: 2em;
    font-weight: bold;
    color: #333;
  }
  
  nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
  
  nav li {
    margin-right: 20px;
  }
  
  .nav-button, .user-profile {
    background-color: #555;
    color: #fff;
    font-weight: bold;
    border: none;
    border-radius: 25px;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
  }
  
  .nav-button:hover, .user-profile:hover {
    background-color: #333;
  }
  main {
    display: flex;
    min-height: calc(100vh - 120px); /* Высота экрана минус высота шапки и футера */
}

.left-side {
    flex: 1; /* Левая часть занимает 1/4 от ширины основной части */
    padding: 20px;
    background-color: #f2f2f2;
}

.right-side {
    flex: 3; /* Правая часть занимает 3/4 от ширины основной части */
    padding: 20px;
    background-color: #e6e6e6;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.footer {
  background-color: #333;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50px;
}
table {
  width: 80%;
  border-collapse: collapse;
  margin: 0; /* Убираем внешний отступ */
}