@import url('https://fonts.googleapis.com/css2?family=Seymour+One&family=Spectral+SC:wght@500;800&display=swap');
.article-container{
    position: relative;
    transition: all 0.100s linear
}
.article-container:hover{
    transform: scale(1.05);
    transition: all 0.100s linear;
}
.divs{
    height: 100px;
    width: 1000px;
}
table {
    width: 80%;
    border-collapse: collapse;
    
}


img {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 0 auto;
    width: 100%;
    mask-image: linear-gradient(to top, transparent -5%, black);
    
}
.article-title{
    position: absolute;
    bottom: 8px;
    left: 16px;
    font-family: 'Spectral SC', serif;
    color: black;
    font-size: 26px;
    font-size: 1vw;
}
.article-topic{
    position: absolute;
    top: 18px;
    left: 16px;
    font-family: 'Seymour One', sans-serif;
    color: white;
    font-size: 20px;
    font-size: 1vw;
}